.wave-container {
  position: relative;
  background: #FFF;
  overflow: hidden;
}

@-webkit-keyframes animateWave {
  0% {
    -webkit-transform: scale(1,0);
            transform: scale(1,0);
  }
  100% {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
}

@keyframes animateWave {
  0% {
    -webkit-transform: scale(1,0);
            transform: scale(1,0);
  }
  100% {
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
}
.wave-container > svg {
  display: block;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-animation: animateWave 2500ms cubic-bezier(0.23, 1, 0.32, 1);
