.cool-text-color-blob:nth-child(3n) {
    padding: 10px 12px;
    border-radius: 60% 50%;
    
}

.cool-text-color-blob:nth-child(3n+1) {
    padding: 11px 10px;
    border-radius: 40% 70%;
}

.cool-text-color-blob:nth-child(3n+2) {
    padding: 9px 13px;
    border-radius: 40% 80%;
}

